* {
  margin: 0%;
  padding: 0%;
}
::-webkit-scrollbar {
  height: 5px;
  width: 0%;
}

::-webkit-scrollbar-track {
  background-color: transparent;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(115, 112, 112);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: rgb(38, 37, 37);
}
body {
  display: flex;
  align-items: center;
  justify-content: center;
  /* padding: 5% 0px ; */
  height: 100vh;
}
.container {
  display: flex;
  flex-direction: column;
  height: 90vh;
  width: 50vw;
  border: solid rgb(30, 87, 113) 2px;
  border-radius: 10px;
  padding-top: 10px;
  align-items: center;
  justify-content: center;
  gap: 1.5vh;
  box-shadow: 0.5px 0.5px 5px 5px rgb(61, 78, 116);
  /* overflow: scroll; */
}
.display {
  height: 18vh;
  width: 47vw;
  /* background-color: rgb(25, 5, 64); */
  background-color: rgb(187, 229, 241);
  border-radius: 10px;
  /* flex-shrink: 0; */
  border: solid rgb(30, 87, 113) 2px;
  text-align: end;
  font-size: 50px;
  overflow-y: scroll;
  box-shadow: inset 0.5px 0.5px 2px 2px rgb(28, 36, 55);
}
.btnholder {
  border: solid rgb(0, 0, 0) 2px;
  width: 47vw;
  border-radius: 10px;
  /* height: 60vh; */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-top: 10px ;
}
.key {
  height: 11vh;
  width: 11vw;
  margin: 2px;
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
  font-size: 25px;
}
.key:hover {
  background-color: rgb(231, 228, 228);
}
.numpad,
.opare {
  display: flex;
  align-items: center;
  /* width: 47vw;
    height: 34vh; */
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.Btn > button {
  /* position: absolute;
  top: 10px;
  left: 10px; */
  border: solid 1px;
  border-radius: 8px;
  padding: 2px 8px;
}

.maincontainer {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 1.6vh;
}
@media only screen and (max-width: 650px) and (min-width:100px){
  .container{
    height: 90vh;
    width: 80vw;
  }
  .display {
    height: 18vh;
    width: 75vw;
  }
  .btnholder {
   width: 75vw;
  }
  .key {
    height: 10vh;
    width: 16vw;
  }

}